<template>
    <div class='warp'>
        <div class='top_box'>
            <p>
                <router-link :to='{path:"/color",query:{id:currId}}'>颜色</router-link>
            </p>
            <p>
                <router-link :to='{path:"/type",query:{id:currId}}'>车款</router-link>
            </p>
            
        </div>
        <section class='section'>
            <ul v-for='(item,index) in imgList' :key='index'>
                <li v-for='(val,key) in item.List' :key='key'>
                    <img :data-src="val.Url" src="../assets/lazyload.jpg"> 
                </li>
             </ul>
             <!-- <div class='des'>
                 <p>外观</p>
                 <p>2470张></p>
             </div> -->
        </section>
    </div>
</template>
<script>
    import {
        mapState,
        mapActions
    } from 'vuex'
    import lazyLoad from '../util/lazyLoad.js'
    export default {
        // data: {
        //     currIds: ''
        // },
        computed: {
            ...mapState({
                imgList: state => state.img.imgList,
                currId: state => state.img.currId
            })
        },
        methods: {
            ...mapActions({
                getImgLists: 'img/getImgLists'
            })
        },
        mounted() {
            console.log(this.$route.query.id);
            // currIds = this.$route.query.id
            this.getImgLists(this.$route.query.id)
            lazyLoad('.section')
        },
    }
</script>
<style scoped lang='scss'>
    .warp {
        width: 100%;
        height: 100%;
        background: #eee;
        display: flex;
        flex-direction: column;
    }
    
    .top_box {
        background: #fff;
        width: 100%;
        height: .8rem;
        -webkit-box-align: center;
        align-items: center;
        box-sizing: border-box;
        z-index: 99;
        display: flex;
        justify-content: space-around;
        p {
            flex: 1;
            font-size: .28rem;
            text-align: center;
            border-right: 1px solid #ccc;
            &:nth-child(2) {
                border-right: none;
            }
        }
    }
    
    .section {
        margin-top: .2rem;
        width: 100%;
        height: 100%;
        overflow: scroll;
        flex: 1;
        ul {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            li {
                width: 30%;
                padding: 0 .1rem;
                img {
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                }
            }
        }
    }
</style>


