export default (el) => {
    //1.找到所有要加载的图片
    let nodes = Array.from(document.querySelectorAll('[data-src]'))
        // console.log(nodes)
        //2.判断图片是否出现在视口中
    let inView = (ele) => {
            let rect = ele.getBoundingClientRect()
            if (rect.top > 0 && rect.top < window.innerHeight && rect.left > 0 && rect.right > 0) {
                return true
            }
            return false
        }
        //3.判断图片是否加载
    let isDeal = (ele) => { // console.log(ele.dataset)
        return ele.src == ele.dataset.src
    }

    let load = () => {
        nodes.forEach((item) => {
            // console.log(item)
            if (inView(item) && !isDeal(item)) {
                item.src = item.dataset.src
            }
        })
    }
    load()
    document.querySelector(el).addEventListener('scroll', load)
}


